import React, {useEffect} from 'react';
import ReactDOM from 'react-dom';
import logo from './logo.svg';
import './App.css';
import Header from './components/header';
import 'bootstrap/dist/css/bootstrap.min.css';
import TaskList from "./components/Task";
import ListItems from './components/List';

class App extends React.Component{

  constructor(props){
    super(props);
    this.state = {
      items: [],
      currentItem:{
        text:'',
        date:'',
        key:''
      }
    }
    this.handleInput = this.handleInput.bind(this);
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
  }

  handleInput(e){
    this.setState({
      currentItem:{
        text: e.target.value,
        key: Date.now()
      }
    })
  }

  // handleDate(e){
  //   this.setState({
  //     currentItem:{
  //       date: e.target.value,
  //       key: Date.now()
  //     }
  //   })
  // }

  addItem(e){
    e.preventDefault();
    const newItem = this.state.currentItem;
    console.log(newItem);
    if(newItem.text!==""){
      const items = [...this.state.items, newItem];
      this.setState({
        items: items,
        currentItem:{
          text:'',
          date:'',
          key:''
        }
      })
    }

    localStorage.setItem('myTODO', JSON.stringify(this.state.items))
    let data = localStorage.getItem('myTODO');
    data = JSON.parse(data);
    console.log(data);
  }

  deleteItem(key){
    const filteredItems = this.state.items.filter(item => item.key !== key);
    this.setState({
      items:filteredItems
    })
    
    localStorage.setItem('myTODO', JSON.stringify(this.state.items))
    let data = localStorage.getItem('myTODO');
    data = JSON.parse(data);
  }

  render(){
    return (
      <div className="App">
        <Header />
        <div className='main'>
            <p>Organize your task better</p>
            <section className="new-task">
                <div className="title">New Task</div>
            </section>
            <form action="" onSubmit={this.addItem}>
                <div className="form-group task-name">
                    <label for="name" >Task Name</label>
                    <input type="text" id="name" name="name" className="form-control" placeholder="New Activity" value={this.state.currentItem.text} onChange={this.handleInput}></input>
                </div>
                <div className="form-group task-date">
                    <label for="due-date">Due Date</label>
                    <input type="date" id="date" name="date" className="form-control" placeholder="02/12/21" value={this.state.currentItem.date} onChange={this.handleDate}></input>
                </div>
                <button type="submit">
                    <span className="add-icon">+</span>
                    <span className="add-text">Add Task</span>
                </button>
            </form>
            <hr></hr>
        </div>
        <TaskList />
          <ListItems items = {this.state.items} deleteItem = {this.deleteItem}></ListItems>
        
      </div>
    )
  }
}


export default App;
